.videodata {
  // min-height: 804px;

  margin: 0 auto;
  padding: 122px 0;
  text-align: center;

  background: linear-gradient(to bottom, $black-dark 706px, $white 706px, $white 98px);
}

.videodata h2 {
  @include font (30px, 30px, 600);

  letter-spacing: -0.005em;

  margin: 0 auto;

  color: $white;
}

.videodata p {
  width: 30%;

  @include font (16px, 28px, 300);

  letter-spacing: -0.005em;

  margin: 0 auto;
  padding-top: 20px;

  color: $white;

  opacity: 0.7;
}

.videodata__video {
  position: relative;

  max-width: 929px;
  margin: 0 auto;

  padding-top: 55px;
}

.videodata img {
  display: block;

  width: 100%;
  height: auto;
}

.videodata button {
  position: absolute;
  left: 50%;
  top: 50%;

  transform: translate(-50%, 0);

  border: none;
  background: none;
}

.videodata svg {
  color: $cyan;

  transition: 0.6s;

  &:hover {
    color: $gray;

    transition: 0.6s;
  }

  &:active {
    color: $black;

    transition: 0.6s;
  }
}

@media (max-width: $tablet-width-only) {
  .videodata {
    padding: 70px 0 50px;

    background: linear-gradient(to bottom, $black-dark 413px, $white 413px, $white 858px);
  }

  .videodata h2 {
    @include font (24px, 30px, 600);
  }

  .videodata p {
    width: 58%;
  }

  .videodata__video {
    max-width: 503px;

    padding-top: 40px;
  }

  .videodata button {
    transform: translate(-50%, -27%);
  }

  .videodata svg {
    width: 90px;
    height: auto;
  }
}

@media (max-width: $mobile-width-only) {
  .videodata {
    padding: 88px 0 10px;

    background: linear-gradient(to bottom, $black-dark 386px, $white 386px, $white 29px);
  }

  .videodata p {
    width: 100%;

    padding-top: 20px;
  }

  .videodata__video {
    max-width: 288px;
    width: 288px;
    margin: 0 auto;

    padding-top: 53px;
  }

  .videodata button {
    transform: translate(-50%, 0);
  }

  .videodata svg {
    width: 65px;
    height: auto;
  }
}
